  .header {
    position: fixed;
    min-width: 1000px;
    width: 100%;
    z-index: 8888;
    top: 0px;
    cursor: pointer;
    transition: 1s;
    &.noheader {
      top: -60px;
      .title {
        height: 70px;
      }
    }

    .title {
      height: 60px;
      padding: 0 150px;
      box-sizing: border-box;
      .logotext {
        float: left;
        cursor: pointer;
        .name {
          line-height: 60px;
          font-size: 26px;
          font-weight: 700;
          color: @TC_B;
        }
      }
      .nav {
        position: absolute;
        top: 0px;
        left: 50%;
        transform: translate(-50%);
        float: left;
        cursor: pointer;
        .nav-item {
          margin: 10px;
          font-size: 14px;
          float: left;
          width: 40px;
          border-radius: 7px;
          transition: 0.3s;
          img {
            display: block;
